import React, {useEffect} from 'react';
import showcaseData from './data/showcase.json';

type ShowcaseT = {
    link: string;
    title: string;
}

export default function Showcase() {
    function mouseMoveHandler(e: { clientX: number; clientY: number; }) {

        // @ts-ignore
        for (const card of document.querySelectorAll(".showcase-cards .card")) {
            const rect = card.getBoundingClientRect(), x = e.clientX - rect.left, y = e.clientY - rect.top;

            card.style.setProperty("--mouse-x", `${x}px`);
            card.style.setProperty("--mouse-y", `${y}px`);
        }


    }

    useEffect(() => {
        // @ts-ignore
        document.querySelector(".showcase-cards").addEventListener("mousemove", mouseMoveHandler);
        return () => {
            // @ts-ignore
            document.querySelector(".showcase-cards").removeEventListener("mousemove", mouseMoveHandler);
        };
    }, []);
    return (
        <div className="cards showcase-cards">
            <h2>Projects</h2>
            <div className={'grid'}>
                {showcaseData.map((v: ShowcaseT, i: number) => {
                    return (<a href={v.link}
                               key={`showcase-${i}`}
                               className={'card small showcase'}
                    >
                        <div className="card-content">{v.title} &rarr;</div>
                    </a>);
                })}
            </div>
        </div>
    );
}
