import React from 'react';
import {SKILLS, SkillType} from "./data/skills.data";

export default function Skills() {
    return (
        <div className="cards skill-cards">
            <h2>Skills</h2>
            <div className={'grid'}>
                {
                    SKILLS
                        .sort((a: SkillType, b: SkillType) => a.name.localeCompare(b.name))
                        .map((skill: SkillType, i: number) => {
                            return (
                                <div key={`skill-${i}`} className={'card small skill'}>
                                    <div className="card-content">{skill.name}</div>
                                </div>
                            );
                        })
                }
            </div>
        </div>
    );
}
